import React, { Component } from "react";
import { MdSearch } from "react-icons/md";
import { PositionAPI } from "../../interfaces";
import searchData from "../../utils/search";

export const isPositionSelected = (
  positionIds: string[],
  thisPosition: string
) => {
  return positionIds.find((item) => item === thisPosition);
};

interface ChoosePositionProps {
  positionList: PositionAPI[];
  setChoosePosition: (value: boolean) => void;
  loadingRequest: boolean;
  defaultMarks: number;
  customMarks: { id: string; marks: number }[];
  setCustomMarks: (
    value: {
      id: string;
      marks: number;
    }[]
  ) => void;
}

interface ChoosePositionState {
  searchInput: string;
}

export class ChoosePosition extends Component<
  ChoosePositionProps,
  ChoosePositionState
> {
  constructor(props: ChoosePositionProps) {
    super(props);

    this.state = {
      searchInput: "",
    };
  }

  setCustomMarks = (thisPosition: string, value: string) => {
    let currentSelectedList: {
      id: string;
      marks: number;
    }[] = [...this.props.customMarks];
    let index = currentSelectedList.findIndex(
      (item) => item.id === thisPosition
    );
    if (index >= 0) {
      currentSelectedList[index].marks = Number(value);
    } else {
      currentSelectedList.push({ id: thisPosition, marks: Number(value) });
    }
    this.props.setCustomMarks(currentSelectedList);
  };

  getMarkByPosition = (thisPosition: string) => {
    return this.props.customMarks.find((item) => item.id === thisPosition);
  };

  setSearchInput = (value: string) => {
    this.setState({ searchInput: value });
  };

  render() {
    return (
      <section className={`rounded`}>
        {/* SEARCH PART */}
        <div className="flex items-center w-full my-1">
          <MdSearch className="w-6 h-6 absolute ml-2" />
          <input
            type="text"
            placeholder="Search"
            className="focus:border-2 h-7 ml-1 pl-9 mr-2 bg-gray-100 border-gray-600 focus:border-gray-700 w-full rounded"
            onChange={(e) => this.setSearchInput(e.target.value)}
            value={this.state.searchInput}
            disabled={this.props.positionList.length > 0 ? false : true}
          />
          <div className="flex items-center w-auto justify-end gap-1">
            <button
              className="bg-red-200 text-red-700 py-1 px-2 mr-1 rounded text-sm focus:outline-none"
              type="button"
              onClick={() => this.props.setChoosePosition(false)}
            >
              Close
            </button>
          </div>
        </div>
        {/* END SEARCH PART ///////////////// */}
        <div
          className={`w-full h-96 mt-2 divide-y divide-gray-300 overflow-y-auto`}
        >
          {searchData(this.props.positionList, this.state.searchInput).length >
          0 ? (
            searchData(this.props.positionList, this.state.searchInput).map(
              (element, index) => (
                <section key={index + element.position_id}>
                  <div
                    className={`group flex items-center justify-between my-1 hover:bg-gray-100`}
                  >
                    <div className="p-1 w-full mr-2">
                      <p className="text-base font-medium text-black">
                        {element.position_title}
                      </p>
                    </div>

                    <input
                      type="number"
                      min={0}
                      max={100}
                      value={
                        this.getMarkByPosition(element.position_id)
                          ? this.getMarkByPosition(element.position_id)!.marks
                          : this.props.defaultMarks
                      }
                      onChange={(e) =>
                        this.setCustomMarks(element.position_id, e.target.value)
                      }
                      className="rounded border-2 border-gray-500 focus:border-gray-700 focus:outline-none"
                    />
                  </div>
                </section>
              )
            )
          ) : (
            <div className="text-center p-3">
              <p className="text-base font-medium text-black">
                No position available
              </p>
            </div>
          )}
        </div>
      </section>
    );
  }
}

export default React.memo(ChoosePosition);
