import axios from "axios";
import React, { Component } from "react";
import { HiOutlineDocumentSearch } from "react-icons/hi";
import { IoWarning } from "react-icons/io5";
import { connect } from "react-redux";
import { PushPopupAlert } from "../../actions";
import { PopupAlertData } from "../../components/Alert/Popup";
import TableLoading from "../../components/Loading/TableLoading";
import NoResult, { NoResultThemes } from "../../components/NoResult/NoResult";
import {
  PositionAPI,
  PositionAPICourse,
  PositionRequirements,
  SessionPlanInterface,
} from "../../interfaces";
import { marksPublicationInterface } from "../../interfaces/marks_publication";
import { PopupAlertTheme } from "../../interfaces/popupAlert";
import { API } from "../../utils/api";
import { errorToText } from "../../utils/errors";

export interface MarksPublicationPosition {
  province_id: string;
  province_name: string;
  courses: PositionAPICourse[];
  position_title: string;
  description: string;
  school_level_id: {
    school_level_name: string;
    description: string;
    education_institute: string[];
    courses: [];
    school_level_id: string;
  };
  education_institute_id: {
    education_category_title: string;
    school_levels: [];
    description: string;
    education_institute_id: string;
  };
  requirements: PositionRequirements[];
  isDeleted: boolean;
  isPublished: boolean;
  position_id: string;
  marks: number;
  candidate_applied: number;
  candidate_passed: number;
}

type SetPassMarksProps = {
  positionList: PositionAPI[];
  marksPublication: marksPublicationInterface[];
  sessions: SessionPlanInterface | "none";
  thisPosition: PositionAPI | null;
  loadingRequest: boolean;
  setThisPosition: (value: PositionAPI | null) => void;

  // FOR UPDATING ==========================
  defaultMarks: number;
  defaultChanged: boolean;
  customMarks: { id: string; marks: number }[];
  setCustomMarks: (
    value: {
      id: string;
      marks: number;
    }[]
  ) => void;
  isUpdateOn: boolean;
  PushPopupAlert: (alert: PopupAlertData) => void;
};

interface SetPassMarksState {
  loading: boolean;
  searchInput: string;
  publicationCount:
    | {
        _id: string;
        position: string;
        marks: number;
        candidate_applied: number;
        candidate_passed: number;
      }[]
    | null;
}
export class SetPassMarks extends Component<
  SetPassMarksProps,
  SetPassMarksState
> {
  constructor(props: SetPassMarksProps) {
    super(props);

    this.state = {
      searchInput: "",
      publicationCount: null,
      loading: false,
    };
  }

  setSearchInput = (value: string) => {
    this.setState({ searchInput: value });
  };

  searchPublishedMarks = (
    objList: MarksPublicationPosition[],
    text: string
  ): MarksPublicationPosition[] => {
    if (undefined === text || text === "") return objList;
    return objList.filter((product) => {
      let flag;
      // let dataKeys = product;

      for (let prop in product) {
        flag = false;
        if (
          prop === "province_name" ||
          prop === "courses" ||
          prop === "position_title" ||
          prop === "description" ||
          prop === "marks" ||
          prop === "candidate_applied" ||
          prop === "candidate_passed"
        ) {
          if (product[prop]) {
            flag =
              product[prop]!.toString()
                .toLowerCase()
                .indexOf(text.toLowerCase()) > -1;
          }
        }
        if (flag) break;
      }
      return flag;
    });
  };

  setCustomMarks = (thisPosition: string, value: string) => {
    if (Number(value) < 0 || Number(value) > 100) {
      return this.props.PushPopupAlert({
        title: `Pass marks must be less or equal to 100 and greater or equal to 0`,
        theme: PopupAlertTheme.danger,
      });
    }

    let currentSelectedList: {
      id: string;
      marks: number;
    }[] = [...this.props.customMarks];
    let index = currentSelectedList.findIndex(
      (item) => item.id === thisPosition
    );

    if (index >= 0) {
      currentSelectedList[index].marks = Number(value);
    } else {
      currentSelectedList.push({ id: thisPosition, marks: Number(value) });
    }

    this.props.setCustomMarks(currentSelectedList);
  };

  getMarkByPosition = (thisPosition: string) => {
    return this.props.customMarks.find((item) => item.id === thisPosition);
  };

  loadPublicationCounts = async (
    allMarks: marksPublicationInterface[],
    sessions: SessionPlanInterface | "none",
    setLoadingRequest: (value: boolean) => void
  ) => {
    const marksBySession =
      sessions !== "none"
        ? allMarks.find((item) => item.session === sessions._id)
        : undefined;
    console.log("====================================");
    console.log({ allMarks, marksBySession });
    console.log("====================================");
    if (marksBySession) {
      console.log("====================================");
      console.log({ marksBySession });
      console.log("====================================");
      setLoadingRequest(true);
      try {
        const res = await axios.get<
          | {
              _id: string;
              position: string;
              marks: number;
              candidate_applied: number;
              candidate_passed: number;
            }[]
        >(
          `${API.PUBLICATION}/publication/count/${marksBySession.publication_id}`
        );

        if (res) {
          console.log("====================================");
          console.log({ loadPublicationCounts: res });
          console.log("====================================");
          this.setPublicationCount(res.data);
        }
      } catch (error) {
        console.error({ loadPublicationCounts_reply: error });
        this.props.PushPopupAlert({
          title: errorToText(error),
          theme: PopupAlertTheme.danger,
        });
      }
      setLoadingRequest(false);
    } else setLoadingRequest(false);
  };

  setPublicationCount = (
    value:
      | {
          _id: string;
          position: string;
          marks: number;
          candidate_applied: number;
          candidate_passed: number;
        }[]
      | null
  ) => {
    this.setState({ publicationCount: value });
  };

  setLoading = (value: boolean) => {
    this.setState({ loading: value });
  };

  componentDidMount = () => {
    if (this.state.publicationCount === null)
      this.loadPublicationCounts(
        this.props.marksPublication,
        this.props.sessions,
        this.setLoading
      );
  };

  getNeededData = (
    allPosition: PositionAPI[],
    allMarks: marksPublicationInterface[],
    sessions: SessionPlanInterface | "none",
    publicationCountData:
      | {
          _id: string;
          position: string;
          marks: number;
          candidate_applied: number;
          candidate_passed: number;
        }[]
      | null
  ): MarksPublicationPosition[] => {
    let result: MarksPublicationPosition[] = [];

    allPosition.forEach((element) => {
      const marksBySession =
        sessions !== "none"
          ? allMarks.find((item) => item.session === sessions._id)
          : undefined;
      if (marksBySession) {
        const marksByPosition = marksBySession.publication_position_marks.find(
          (item) => item.position === element.position_id
        );
        const publicationCountByPosition =
          publicationCountData &&
          publicationCountData.find(
            (item) => item.position === element.position_id
          );
        if (marksByPosition) {
          result.push({
            province_id: element.province_id,
            province_name: element.province_name,
            courses: element.courses,
            position_title: element.position_title,
            description: element.description,
            school_level_id: element.school_level_id,
            education_institute_id: element.education_institute_id,
            requirements: element.requirements,
            isDeleted: element.isDeleted,
            isPublished: element.isPublished,
            position_id: element.position_id,
            marks: marksByPosition.marks,
            candidate_applied: publicationCountByPosition
              ? publicationCountByPosition.candidate_applied
              : 0,
            candidate_passed: publicationCountByPosition
              ? publicationCountByPosition.candidate_passed
              : 0,
          });
        }
      }
    });

    return result;
  };

  render() {
    if (this.props.sessions === "none") {
      return (
        <div className="py-10 flex flex-col items-center rounded border border-gray-300 bg-white text-2xl font-medium text-yellow-800 animate__animated animate__pulse">
          <IoWarning className="w-32 h-32" />
          No Active session available!
        </div>
      );
    }

    return (
      <div>
        {this.props.positionList.length <= 0 && (
          <div className="text-gray-400 px-6 py-4 rounded border mt-4 text-xl font-extrabold flex flex-col items-center justify-center bg-gray-100">
            <HiOutlineDocumentSearch className="text-7xl text-gray-400 animate__animated animate__shakeX" />
            <span>No position available</span>
          </div>
        )}

        {this.props.positionList.length > 0 && (
          <section className="flex flex-col-reverse sm:flex-row items-center gap-2 w-full mb-2">
            <div className="w-full">
              <input
                onChange={(e: React.ChangeEvent<HTMLInputElement>) =>
                  this.setSearchInput(e.target.value)
                }
                type="text"
                className="input-white h-10 text-sm border-2 border-gray-400 focus:border-gray-500 w-full"
                placeholder={`Search position by title or description`}
                value={this.state.searchInput}
                disabled={
                  this.state.publicationCount &&
                  this.getNeededData(
                    this.props.positionList,
                    this.props.marksPublication,
                    this.props.sessions,
                    this.state.publicationCount
                  ).length > 0
                    ? false
                    : true
                }
              />
            </div>
          </section>
        )}

        <table className="w-full">
          <thead className="bg-white border-b-2 border-gray-300 text-gray-600 font-bold text-sm">
            <tr>
              <td className="px-2 py-2">Positions</td>
              <td className="px-2 py-2">All candidates</td>
              <td className="px-2 py-2">Passed candidates</td>
              <td className="px-2 py-2">Pass marks</td>
            </tr>
          </thead>
          <tbody>
            {!this.state.loading ? (
              this.searchPublishedMarks(
                this.getNeededData(
                  this.props.positionList,
                  this.props.marksPublication,
                  this.props.sessions,
                  this.state.publicationCount
                ),
                this.state.searchInput
              ).length > 0 ? (
                this.searchPublishedMarks(
                  this.getNeededData(
                    this.props.positionList,
                    this.props.marksPublication,
                    this.props.sessions,
                    this.state.publicationCount
                  ),
                  this.state.searchInput
                ).map((item: MarksPublicationPosition, i: number) => (
                  <tr
                    key={i + item.position_id}
                    className={`${
                      i % 2 !== 0 ? "bg-gray-100" : "bg-white"
                    } hover:bg-blue-100 hover:text-blue-700 border-b hover:border-blue-500 text-base text-gray-600 border-gray-300`}
                  >
                    <td className="px-2 py-2">{item.position_title}</td>
                    <td className="px-2 py-2">{item.candidate_applied}</td>
                    <td className="px-2 py-2">{item.candidate_passed}</td>
                    <td className="px-2 py-2">
                      {!this.props.isUpdateOn ? (
                        <span className="text-gray-600 italic font-semibold">
                          {item.marks}%
                        </span>
                      ) : (
                        <input
                          type="number"
                          min={0}
                          max={100}
                          value={
                            this.getMarkByPosition(item.position_id)
                              ? this.getMarkByPosition(item.position_id)!.marks
                              : this.props.defaultChanged
                              ? this.props.defaultMarks
                              : item.marks
                          }
                          onChange={(e) =>
                            this.setCustomMarks(
                              item.position_id,
                              e.target.value
                            )
                          }
                          className="rounded border-2 border-gray-500 w-full focus:border-gray-700 focus:outline-none"
                        />
                      )}
                    </td>
                  </tr>
                ))
              ) : (
                <tr>
                  <td colSpan={8} className="px-2 py-2">
                    <NoResult
                      theme={NoResultThemes.Dark}
                      value={"No pass marks has been set"}
                    />
                  </td>
                </tr>
              )
            ) : (
              <TableLoading cols={4} />
            )}
          </tbody>
        </table>
      </div>
    );
  }
}

export default React.memo(connect(null, { PushPopupAlert })(SetPassMarks));
