import React, { Component } from "react";
import axios from "axios";
import { MdArrowBack } from "react-icons/md";
import { RiFolderSettingsFill } from "react-icons/ri";
import { connect } from "react-redux";
import { Auth, PushPopupAlert } from "../../actions";
import {
  FC_GetMarksPublicationByInstitution,
  updateMarksPublication,
} from "../../actions/marks_publication";
import { FC_GetActiveSessionPlan } from "../../actions/session";
import { FC_GetPositionsByInstitution } from "../../actions/settings";
import { PopupAlertData } from "../../components/Alert/Popup";
import { PositionAPI, SessionPlanInterface } from "../../interfaces";
import {
  marksPublicationInterface,
  publicationInterface,
} from "../../interfaces/marks_publication";
import { PopupAlertTheme } from "../../interfaces/popupAlert";
import { StoreState } from "../../reducers";
import { API } from "../../utils/api";
import { errorToText } from "../../utils/errors";
import MarksPublicationForm from "./MarksPublicationForm";
import SetPassMarks from "./SetPassMarks";
import { FC_HasInstitute } from "../../utils/functions";
import NoSession from "../SessionSettings/NoSession";

export const marksArray = (update: boolean = false): (number | null)[] => {
  let result: (number | null)[] = update ? [null] : [0],
    n: number = 0;
  while (n <= 100) {
    result.push(n);
    n++;
  }
  return result;
};

interface MarksPublicationProps {
  auth: Auth;
  systemPositions: PositionAPI[] | null;
  sessions: SessionPlanInterface | "none" | null;
  marksPublication: marksPublicationInterface[] | null;
  FC_GetActiveSessionPlan: Function;
  FC_GetPositionsByInstitution: Function;
  FC_GetMarksPublicationByInstitution: Function;
  updateMarksPublication: Function;
  PushPopupAlert: (alert: PopupAlertData) => void;
}

interface MarksPublicationState {
  loading: boolean;
  loadingRequest: boolean;
  modalAction: "CREATE" | "UPDATE" | false;
  searchPosition: string;
  positionId: string[];
  thisPosition: PositionAPI | null;
  inputMarks: number;
  inputComment: string;
  customMarks: { id: string; marks: number }[];
  defaultChanged: boolean;
}

class index extends Component<MarksPublicationProps, MarksPublicationState> {
  constructor(props: MarksPublicationProps) {
    super(props);

    this.state = {
      loading: true,
      loadingRequest: false,
      modalAction: false,
      searchPosition: "",
      positionId: [],
      thisPosition: null,
      inputMarks: 70,
      inputComment: "",
      customMarks: [],
      defaultChanged: false,
    };
  }

  setLoading = (status: boolean) => {
    this.setState({ loading: status });
  };

  setLoadingRequest = (status: boolean) => {
    this.setState({ loadingRequest: status });
  };

  setModalAction = (status: "CREATE" | "UPDATE" | false) => {
    this.setState({ modalAction: status });
  };

  setThisPosition = (value: PositionAPI | null) => {
    this.setState({ thisPosition: value });
  };

  setPositionId = (value: string[]) => {
    this.setState({
      positionId: value,
    });
  };

  setCustomMarks = (value: { id: string; marks: number }[]) => {
    this.setState({
      customMarks: value,
    });
  };

  setInputMarks = (value: number) => {
    this.setState({ inputMarks: value });
  };

  setInputComment = (value: string) => {
    this.setState({ inputComment: value });
  };

  loadPositions = () => {
    if (
      this.props.systemPositions === null &&
      this.props.auth.education_institution_id
    ) {
      this.props.FC_GetPositionsByInstitution(
        this.props.auth.education_institution_id,
        (status: boolean, msg: string) => {
          this.setLoading(status);
          console.log({ status: status, msg: msg });
          if (!status && msg.length) {
            this.props.PushPopupAlert({
              title: msg,
              theme: PopupAlertTheme.danger,
            });
          }
        }
      );
    } else if (this.state.loading) {
      this.setLoading(false);
    }
  };

  componentDidMount = () => {
    if (FC_HasInstitute(this.props.auth.education_institution_id)) {
      // SESSION CONTROLLER ========================================
      if (this.props.sessions === null || this.props.sessions === "none") {
        this.props.FC_GetActiveSessionPlan(
          this.props.auth.education_institution_id!,
          (
            status: boolean,
            msg: string,
            data: SessionPlanInterface | "none"
          ) => {
            this.setLoading(status);
            if (!status) {
              if (msg.length) {
                this.props.PushPopupAlert({
                  title: msg,
                  theme: PopupAlertTheme.danger,
                });
              }

              if (data && data !== "none") {
                this.loadPositions();
              }
            }
          }
        );
      } else {
        this.loadPositions();
      }

      if (this.props.marksPublication === null) {
        this.props.FC_GetMarksPublicationByInstitution(
          this.props.auth.education_institution_id!,
          (
            status: boolean,
            msg: string,
            data?: marksPublicationInterface[]
          ) => {
            this.setLoading(status);
            if (!status && msg.length > 0) {
              this.props.PushPopupAlert({
                title: msg,
                theme: PopupAlertTheme.danger,
              });
            }

            if (!status && data) {
              console.log("====================================");
              console.log({ data });
              console.log("====================================");
            }
          }
        );
      }
    }
  };

  FC_neededPositions = (
    allPosition: PositionAPI[]
  ): {
    withoutMarks: PositionAPI[];
    withMarks: PositionAPI[];
  } => {
    let result: {
      withoutMarks: PositionAPI[];
      withMarks: PositionAPI[];
    } = { withMarks: [], withoutMarks: [] };
    let publishedPosition: PositionAPI[] = allPosition.filter(
      (item) => item.isPublished && !item.isDeleted
    );

    result.withoutMarks = publishedPosition;

    return result;
  };

  isMarkPublished = (
    marksPublication: marksPublicationInterface[],
    session: SessionPlanInterface
  ): boolean => {
    if (marksPublication.length > 0) {
      const thisPublishedMarks = marksPublication.find(
        (item) => item.session === session._id
      );
      if (
        thisPublishedMarks &&
        thisPublishedMarks.publication_status === "PUBLISHED"
      ) {
        return true;
      }
    }
    return false;
  };

  isUpdateAllowed = (
    marksPublication: marksPublicationInterface[],
    session: SessionPlanInterface
  ): boolean => {
    if (marksPublication.length > 0) {
      const thisPublishedMarks = marksPublication.find(
        (item) => item.session === session._id
      );
      if (thisPublishedMarks) {
        return true;
      }
    }
    return false;
  };

  changeDefaultMarks = (value: number) => {
    if (this.state.customMarks.length > 0) {
      if (
        window.confirm(
          "Do you want the default pass mark to override the customized pass marks on some position"
        )
      ) {
        this.setCustomMarks([]);
      }
    }
    this.setState({ defaultChanged: true });
    this.setInputMarks(value);
  };

  handleUpdate = async () => {
    if (this.props.sessions === null) {
      return this.props.PushPopupAlert({
        title: `Load session first!`,
        theme: PopupAlertTheme.info,
      });
    }

    if (this.props.sessions === "none") {
      return this.props.PushPopupAlert({
        title: `No active session available!`,
        theme: PopupAlertTheme.info,
      });
    }

    if (this.props.marksPublication === null) {
      return this.props.PushPopupAlert({
        title: `Load published pass marks first!`,
        theme: PopupAlertTheme.info,
      });
    }

    const thisSession = this.props.sessions;

    const thisPublishedMarks = this.props.marksPublication.find(
      (item) => item.session === thisSession._id
    );

    if (!thisPublishedMarks) {
      return this.props.PushPopupAlert({
        title: `No pass marks publication available! Add pass marks instead!`,
        theme: PopupAlertTheme.info,
      });
    }

    if (this.state.customMarks.length <= 0 && !this.state.defaultChanged) {
      return this.props.PushPopupAlert({
        title: `No change have been done!`,
        theme: PopupAlertTheme.info,
      });
    }

    if (!window.confirm(`Are you sure you wanted to update`)) {
      return;
    }

    let requestData: marksPublicationInterface,
      positionSetPassMarks: publicationInterface[] = [];

    thisPublishedMarks.publication_position_marks.forEach((positionItem) => {
      if (this.state.customMarks.length > 0) {
        const thisCustomMarks = this.state.customMarks.find(
          (item) => item.id === positionItem.position
        );
        if (thisCustomMarks) {
          positionSetPassMarks.push({
            _id: positionItem._id,
            position: thisCustomMarks.id,
            marks: thisCustomMarks.marks,
            comment: positionItem.comment,
          });
        } else {
          if (this.state.defaultChanged) {
            positionSetPassMarks.push({
              _id: positionItem._id,
              position: positionItem.position,
              marks: this.state.inputMarks,
              comment: positionItem.comment,
            });
          } else {
            positionSetPassMarks.push({
              _id: positionItem._id,
              position: positionItem.position,
              marks: positionItem.marks,
              comment: positionItem.comment,
            });
          }
        }
      } else {
        if (this.state.defaultChanged) {
          positionSetPassMarks.push({
            _id: positionItem._id,
            position: positionItem.position,
            marks: this.state.inputMarks,
            comment: positionItem.comment,
          });
        } else {
          positionSetPassMarks.push({
            _id: positionItem._id,
            position: positionItem.position,
            marks: positionItem.marks,
            comment: positionItem.comment,
          });
        }
      }
    });

    requestData = {
      session: thisPublishedMarks.session,
      doneby: this.props.auth.user.user_id,
      created_date: thisPublishedMarks.created_date,
      publication_position_marks: positionSetPassMarks,
      publication_id: thisPublishedMarks.publication_id,
      publication_status: thisPublishedMarks.publication_status,
      status: thisPublishedMarks.status,
      createdAt: thisPublishedMarks.createdAt,
      updatedAt: thisPublishedMarks.updatedAt,
      institution: thisPublishedMarks.institution,
    };

    console.log("====================================");
    console.log({ requestData });
    console.log("====================================");

    this.setLoadingRequest(true);
    try {
      const res = await axios.put<marksPublicationInterface>(
        `${API.PUBLICATION}/publication/${thisPublishedMarks.publication_id}`,
        requestData
      );
      if (res) {
        console.log("====================================");
        console.log({ res });
        console.log("====================================");
        // this.props.verifyDocument(api_redux);
        this.props.updateMarksPublication(res.data);
        this.props.PushPopupAlert({
          title: `Pass mark updated successfully!`,
          theme: PopupAlertTheme.success,
        });
      }
      this.cleanBeforeClose();
    } catch (error) {
      console.error(error);
      this.props.PushPopupAlert({
        title: errorToText(error),
        theme: PopupAlertTheme.danger,
      });
    }
    this.setLoadingRequest(false);
  };

  getWaitingList = async (sessionId: string, callback: Function) => {
    callback(true, "");
    try {
      const res = await axios.get<marksPublicationInterface>(
        `${API.DO_EXAM}/waiting/publication/${sessionId}`
      );
      if (res) {
        console.log("====================================");
        console.log({ getWaitingList: res });
        console.log("====================================");
        callback(false, "");
      }
    } catch (error) {
      console.error({ getWaitingListError: error });
      callback(false, errorToText(error));
      this.props.PushPopupAlert({
        title: errorToText(error),
        theme: PopupAlertTheme.danger,
      });
    }
    this.setLoadingRequest(false);
  };

  handlePublishMarks = async () => {
    if (this.props.sessions === null) {
      return this.props.PushPopupAlert({
        title: `Load session first!`,
        theme: PopupAlertTheme.info,
      });
    }

    if (this.props.sessions === "none") {
      return this.props.PushPopupAlert({
        title: `No active session available!`,
        theme: PopupAlertTheme.info,
      });
    }

    if (this.props.marksPublication === null) {
      return this.props.PushPopupAlert({
        title: `Load published pass marks first!`,
        theme: PopupAlertTheme.info,
      });
    }

    const thisSession = this.props.sessions;

    const thisPublishedMarks = this.props.marksPublication.find(
      (item) => item.session === thisSession._id
    );

    if (!thisPublishedMarks) {
      return this.props.PushPopupAlert({
        title: `No pass marks publication available! Add pass marks instead!`,
        theme: PopupAlertTheme.info,
      });
    }

    if (!window.confirm(`Are you sure you wanted to publish marks`)) {
      return;
    }

    console.log("====================================");
    console.log({ thisPublishedMarks: thisPublishedMarks.publication_id });
    console.log("====================================");

    this.setLoadingRequest(true);
    await this.getWaitingList(
      thisSession._id,
      async (status: boolean, msg: string) => {
        if (!status && msg.length === 0) {
          try {
            const res = await axios.put<marksPublicationInterface>(
              `${API.PUBLICATION}/publication/publication/${thisPublishedMarks.publication_id}`
            );
            if (res && this.props.auth.education_institution_id) {
              console.log("====================================");
              console.log({ res });
              console.log("====================================");
              // this.props.verifyDocument(api_redux);
              this.props.FC_GetMarksPublicationByInstitution(
                this.props.auth.education_institution_id,
                (status: boolean, msg: string) => {
                  if (!status && msg.length > 0) {
                    this.props.PushPopupAlert({
                      title: msg,
                      theme: PopupAlertTheme.danger,
                    });
                  }
                }
              );
              // this.props.updateMarksPublication(res.data);
              this.props.PushPopupAlert({
                title: `Pass mark published successfully!`,
                theme: PopupAlertTheme.success,
              });
            }
            this.cleanBeforeClose();
          } catch (error) {
            console.error(error);
            this.props.PushPopupAlert({
              title: errorToText(error),
              theme: PopupAlertTheme.danger,
            });
          }
        }
      }
    );
    this.setLoadingRequest(false);
  };

  cleanBeforeClose = () => {
    if (this.state.modalAction === "UPDATE") {
      this.setInputMarks(70);
      this.setInputComment("");
      this.setCustomMarks([]);
      this.setModalAction(false);
      this.setState({ defaultChanged: false });
    }
  };

  render() {
    if (FC_HasInstitute(this.props.auth.education_institution_id) === false) {
      return (
        <NoSession
          title="Authorization Error"
          description="Please contact the administration to get the access."
        />
      );
    }

    if (this.props.sessions === null || this.state.loading) {
      return (
        <div className="text-center mt-7 font-bold text-4xl text-gray-400 animate-pulse">
          Loading...
        </div>
      );
    }

    return (
      <div>
        <div className="mt-3 rounded flex flex-row justify-between items-center">
          <div className="flex flex-row items-center gap-3">
            <RiFolderSettingsFill className="text-6xl text-blue-600" />
            <div className="flex flex-col">
              <span className="text-blue-600 text-2xl font-bold">
                Marks publication
              </span>
              <div className="flex items-center gap-1">
                <span className="text-gray-600 text-base italic">Session:</span>
                <span className="text-gray-600 text-base font-bold">
                  {this.props.sessions &&
                    this.props.sessions !== "none" &&
                    this.props.sessions.session_plan_name}
                </span>
              </div>
            </div>
          </div>
        </div>

        {/* CONTENT BODY =============================== */}
        <div className="mt-1 p-1 bg-white">
          {/* CONTENT BODY TITLE ========================== */}
          {this.props.sessions !== "none" && (
            <div
              className={`flex flex-col sm:flex-row items-center gap-1 bg-gray-100 py-1 mb-2 ${
                this.props.sessions && "justify-between"
              }`}
            >
              <div className="flex items-center gap-x-2">
                {/* BACK BUTTON CONTROLLER ============== */}
                {this.props.sessions && this.state.modalAction === "CREATE" && (
                  <button
                    className={`ml-3 bg-gray-50 hover:bg-blue-100 rounded-full px-1 py-1 focus:outline-none ${
                      this.state.loadingRequest
                        ? "cursor-not-allowed"
                        : "cursor-pointer"
                    } animate__animated animate__fadeIn`}
                    disabled={this.state.loadingRequest}
                    onClick={() =>
                      this.setState({
                        modalAction: false,
                        positionId: [],
                        inputMarks: 70,
                        inputComment: "",
                      })
                    }
                  >
                    <MdArrowBack className="w-7 h-7" />
                  </button>
                )}
                {/* END BACK BUTTON CONTROLLER ============== */}
                {/* ----------------------------------------------------------------- */}
              </div>

              <div className="flex items-center">
                {/* ADD PASS MARKS PUBLICATION */}
                {this.props.sessions &&
                  this.state.modalAction === false &&
                  this.props.systemPositions &&
                  this.props.systemPositions.filter(
                    (item) => item.isPublished && !item.isDeleted
                  ).length > 0 &&
                  this.props.marksPublication &&
                  !this.isUpdateAllowed(
                    this.props.marksPublication,
                    this.props.sessions
                  ) && (
                    <div
                      className="btn bg-blue-600 text-white cursor-pointer hover:bg-blue-800"
                      onClick={() => {
                        this.setModalAction("CREATE");
                      }}
                    >
                      Add pass marks
                    </div>
                  )}

                {/* UPDATE PASS MARKS PUBLICATION */}
                {this.props.sessions &&
                  this.state.modalAction === false &&
                  this.props.systemPositions &&
                  this.props.systemPositions.filter(
                    (item) => item.isPublished && !item.isDeleted
                  ).length > 0 &&
                  this.props.marksPublication &&
                  this.isUpdateAllowed(
                    this.props.marksPublication,
                    this.props.sessions
                  ) &&
                  !this.isMarkPublished(
                    this.props.marksPublication,
                    this.props.sessions
                  ) && (
                    <div
                      className="btn bg-gray-300 text-blue-600 hover:text-white cursor-pointer hover:bg-blue-800"
                      onClick={() => {
                        this.setModalAction("UPDATE");
                        this.setInputMarks(-1);
                      }}
                    >
                      Update pass marks
                    </div>
                  )}

                {/* PUBLISH MARKS PUBLICATION */}
                {this.props.sessions &&
                  this.state.modalAction === false &&
                  this.props.systemPositions &&
                  this.props.systemPositions.filter(
                    (item) => item.isPublished && !item.isDeleted
                  ).length > 0 &&
                  this.props.marksPublication &&
                  this.isUpdateAllowed(
                    this.props.marksPublication,
                    this.props.sessions
                  ) &&
                  !this.isMarkPublished(
                    this.props.marksPublication,
                    this.props.sessions
                  ) && (
                    <div
                      className={`btn bg-green-600 text-white ml-1 hover:bg-green-800 ${
                        this.state.loadingRequest
                          ? "cursor-wait"
                          : "cursor-pointer"
                      }`}
                      onClick={() =>
                        !this.state.loadingRequest && this.handlePublishMarks()
                      }
                    >
                      Publish pass marks
                    </div>
                  )}

                {this.props.marksPublication &&
                  this.isMarkPublished(
                    this.props.marksPublication,
                    this.props.sessions
                  ) && (
                    <div className="px-3 py-1 text-1xl font-bold text-blue-500 border-4 border-blue-500 border-dotted">
                      Marks published
                    </div>
                  )}

                {this.props.sessions &&
                  this.state.modalAction === "UPDATE" &&
                  this.props.systemPositions &&
                  this.props.systemPositions.filter(
                    (item) => item.isPublished && !item.isDeleted
                  ).length > 0 &&
                  this.props.marksPublication &&
                  this.isUpdateAllowed(
                    this.props.marksPublication,
                    this.props.sessions
                  ) && (
                    <div className="flex items-center gap-1 animate__animated animate__fadeInRight animate__faster">
                      <div className="flex items-center gap-1">
                        <label className="w-full inline-block text-right">
                          Default pass marks
                        </label>
                        <select
                          className="outline-none bg-white text-gray-700 text-base mr-1 py-2 px-2 border-2 border-gray-500 rounded"
                          value={this.state.inputMarks}
                          onChange={(e) =>
                            this.changeDefaultMarks(Number(e.target.value))
                          }
                        >
                          {marksArray(true).map((item, index) => (
                            <option
                              value={item ? item : "-1"}
                              key={index * (item ? item : -1)}
                              disabled={item === null}
                            >
                              {item ? item : "Select"}
                            </option>
                          ))}
                        </select>
                      </div>
                      <button
                        className={`btn bg-primary text-white hover:bg-blue-600 focus:outline-none ${
                          this.state.loadingRequest && "cursor-wait"
                        }`}
                        type="button"
                        onClick={() => this.handleUpdate()}
                        disabled={this.state.loadingRequest}
                      >
                        {this.state.loadingRequest ? "Loading..." : "Save"}
                      </button>
                      <button
                        className={`btn bg-red-500 text-white hover:bg-red-600 focus:outline-none ${
                          this.state.loadingRequest && "cursor-wait"
                        }`}
                        type="button"
                        onClick={() => this.cleanBeforeClose()}
                        disabled={this.state.loadingRequest}
                      >
                        Cancel
                      </button>
                    </div>
                  )}
              </div>
            </div>
          )}
          {/* END CONTENT BODY TITLE ========================== */}

          {/* REAL BODY ============================= */}
          <div className="=rounded mx-1 mb-2 overflow-x-auto">
            {this.props.sessions === "none" && (
              <NoSession description="No active session availabl!e" />
            )}
            {this.state.modalAction !== "CREATE" &&
              this.props.systemPositions &&
              this.props.marksPublication && (
                <SetPassMarks
                  positionList={this.props.systemPositions.filter(
                    (item) => item.isPublished && !item.isDeleted
                  )}
                  marksPublication={this.props.marksPublication}
                  sessions={this.props.sessions}
                  thisPosition={this.state.thisPosition}
                  loadingRequest={this.state.loadingRequest}
                  isUpdateOn={this.state.modalAction === "UPDATE"}
                  setThisPosition={this.setThisPosition}
                  customMarks={this.state.customMarks}
                  setCustomMarks={this.setCustomMarks}
                  defaultChanged={this.state.defaultChanged}
                  defaultMarks={this.state.inputMarks}
                />
              )}
          </div>
        </div>
        {/* END CONTENT BODY =========================== */}

        {/* modal controller ==================================== */}
        {this.props.sessions &&
          this.props.sessions !== "none" &&
          this.state.modalAction === "CREATE" &&
          this.props.systemPositions && (
            <MarksPublicationForm
              sessions={this.props.sessions}
              positionList={
                this.FC_neededPositions(this.props.systemPositions).withoutMarks
              }
              loadingRequest={this.state.loadingRequest}
              inputMarks={this.state.inputMarks}
              modalAction={this.state.modalAction}
              setModalAction={this.setModalAction}
              setInputMarks={this.setInputMarks}
              inputComment={this.state.inputComment}
              setInputComment={this.setInputComment}
              setLoadingRequest={this.setLoadingRequest}
              customMarks={this.state.customMarks}
              setCustomMarks={this.setCustomMarks}
            />
          )}
      </div>
    );
  }
}

const mapStateToProps = ({
  auth,
  sessionPlan,
  marksPublication,
  system_settings,
}: StoreState): {
  auth: Auth;
  sessions: SessionPlanInterface | "none" | null;
  marksPublication: marksPublicationInterface[] | null;
  systemPositions: PositionAPI[] | null;
} => {
  return {
    auth: auth,
    systemPositions: system_settings.systemPositions,
    marksPublication,
    sessions: sessionPlan.activeSessionPlan,
  };
};

export const MarksPublication = connect(mapStateToProps, {
  FC_GetPositionsByInstitution,
  FC_GetActiveSessionPlan,
  FC_GetMarksPublicationByInstitution,
  updateMarksPublication,
  PushPopupAlert,
})(index);
