import axios from "axios";
import { Dispatch } from "redux";
import { ActionTypes } from "./types";
import { API } from "../utils/api";
import { setAxiosToken } from "../utils/AxiosToken";
import { errorToText } from "../utils/errors";
import { marksPublicationInterface } from "../interfaces/marks_publication";

/**
 * * ****************************** INTERFACES *****************************
 */

// MARKS PUBLICATION
export interface GetMarksPublicationAction {
  type: ActionTypes.GET_MARKS_PUBLICATION;
  payload: marksPublicationInterface[];
}

// CREATE PERSONAL CLAIM
export interface CreateMarksPublicationAction {
  type: ActionTypes.CREATE_MARKS_PUBLICATION;
  payload: marksPublicationInterface;
}

// UPDATE PERSONAL CLAIM
export interface UpdateMarksPublicationAction {
  type: ActionTypes.UPDATE_MARKS_PUBLICATION;
  payload: marksPublicationInterface;
}

export type MarksPublicationActionType =
  | GetMarksPublicationAction
  | CreateMarksPublicationAction
  | UpdateMarksPublicationAction;

/**
 * * ****************************** ACTIONS *****************************
 */

// GET MARKS PUBLICATION ==========================
export const getMarksPublication = (callback: Function) => {
  callback(true, "");
  return async (dispatch: Dispatch) => {
    try {
      setAxiosToken();
      const res = await axios.get<marksPublicationInterface[]>(
        `${API.PUBLICATION}/publication`
      );
      let responseData: marksPublicationInterface[] = [];
      if (res.status === 200) {
        // setApplications(res.data);
        if (res.data !== null && res.data.length > 0) {
          responseData = res.data;
        }
      }
      console.log("====================================");
      console.log({ GetMarksPublication: res.data });
      console.log("====================================");
      dispatch<GetMarksPublicationAction>({
        type: ActionTypes.GET_MARKS_PUBLICATION,
        payload: responseData,
      });
      callback(false, "", responseData);
    } catch (error) {
      console.error({ error: errorToText(error) });
      callback(false, errorToText(error));
    }
  };
};

