import axios from "axios";
import moment from "moment";
import React, { Component } from "react";
import { connect } from "react-redux";
import { Auth, PushPopupAlert } from "../../actions";
import { createMarksPublication } from "../../actions/marks_publication";
import { PopupAlertData } from "../../components/Alert/Popup";
import { PositionAPI, SessionPlanInterface } from "../../interfaces";
import {
  marksPublicationFormInterface,
  marksPublicationInterface,
  publicationFormInterface,
} from "../../interfaces/marks_publication";
import { PopupAlertTheme } from "../../interfaces/popupAlert";
import { StoreState } from "../../reducers";
import { capitalizeFirstLetter, isEmptyOrSpaces } from "../../shared/dataTest";
import { API } from "../../utils/api";
import { errorToText } from "../../utils/errors";
import ChoosePosition from "./ChoosePosition";
import { marksArray } from "./MarksPublication";

interface MarksPublicationFormProps {
  sessions: SessionPlanInterface;
  positionList: PositionAPI[];
  loadingRequest: boolean;
  inputMarks: number;
  inputComment: string;
  customMarks: { id: string; marks: number }[];
  setCustomMarks: (
    value: {
      id: string;
      marks: number;
    }[]
  ) => void;
  modalAction: false | "CREATE" | "UPDATE";
  setModalAction: (status: "CREATE" | "UPDATE" | false) => void;
  setInputMarks: (value: number) => void;
  setInputComment: (value: string) => void;
  setLoadingRequest: (status: boolean) => void;

  //   REDUX ==========================
  auth: Auth;
  createMarksPublication: Function;
  PushPopupAlert: (alert: PopupAlertData) => void;
}

interface MarksPublicationFormState {
  searchPosition: string;
  formError: {
    element: string;
    msg: string;
  } | null;
  choosePosition: boolean;
}

class MarksPublicationForm extends Component<
  MarksPublicationFormProps,
  MarksPublicationFormState
> {
  constructor(props: MarksPublicationFormProps) {
    super(props);

    this.state = {
      searchPosition: "",
      formError: null,
      choosePosition: false,
    };
  }

  setChoosePosition = (value: boolean) => {
    this.setState({ choosePosition: value });
  };

  setFormError = (
    value: {
      element: string;
      msg: string;
    } | null
  ) => {
    this.setState({ formError: value });
  };

  changeDefaultMarks = (value: number) => {
    if (this.props.customMarks.length > 0) {
      if (
        window.confirm(
          "Do you want the default pass mark to override the customized pass marks on some position"
        )
      ) {
        this.props.setCustomMarks([]);
      }
    }
    this.props.setInputMarks(value);
  };

  cleanBeforeClose = () => {
    if (this.props.modalAction === "CREATE") {
      this.props.setInputMarks(70);
      this.props.setInputComment("");
      this.props.setCustomMarks([]);
      this.props.setModalAction(false);
    }
  };

  handleSubmit = async (e: React.FormEvent<HTMLFormElement>) => {
    e.preventDefault();
    if (
      this.props.auth.education_institution_id === null ||
      isEmptyOrSpaces(this.props.auth.education_institution_id)
    )
      return this.props.PushPopupAlert({
        title: `You don't have access for this action!`,
        theme: PopupAlertTheme.warning,
      });
    if (this.props.positionList.length <= 0) {
      return this.setFormError({
        element: "position",
        msg: "There is no published position available",
      });
    }

    if (isEmptyOrSpaces(this.props.inputComment)) {
      return this.setFormError({
        element: "comment",
        msg: "Comment is required",
      });
    }

    if (!window.confirm(`Are you sure you wanted to submit`)) {
      return;
    }

    let requestData: marksPublicationFormInterface,
      positionWithMarks: publicationFormInterface[] = [];

    this.props.positionList.forEach((positionItem) => {
      if (this.props.customMarks.length > 0) {
        const thisCustomMarks = this.props.customMarks.find(
          (item) => item.id === positionItem.position_id
        );
        if (thisCustomMarks) {
          positionWithMarks.push({
            position: thisCustomMarks.id,
            marks: thisCustomMarks.marks,
            comment: this.props.inputComment,
          });
        } else {
          positionWithMarks.push({
            position: positionItem.position_id,
            marks: this.props.inputMarks,
            comment: this.props.inputComment,
          });
        }
      } else {
        positionWithMarks.push({
          position: positionItem.position_id,
          marks: this.props.inputMarks,
          comment: this.props.inputComment,
        });
      }
    });

    requestData = {
      session: this.props.sessions._id,
      doneby: this.props.auth.user.user_id,
      created_date: moment(moment().toDate()).format("YYYY-MM-DD").toString(),
      publication_position_marks: positionWithMarks,
      institution: this.props.auth.education_institution_id,
    };

    console.log("====================================");
    console.log({ requestData });
    console.log("====================================");

    this.props.setLoadingRequest(true);
    try {
      const res = await axios.post<marksPublicationInterface>(
        `${API.PUBLICATION}/publication`,
        requestData
      );
      if (res) {
        console.log("====================================");
        console.log({ res });
        console.log("====================================");
        this.props.createMarksPublication(res.data);
        this.props.PushPopupAlert({
          title: `Mark published successfully!`,
          theme: PopupAlertTheme.success,
        });
      }
      this.cleanBeforeClose();
    } catch (error) {
      console.error(error);
      this.props.PushPopupAlert({
        title: errorToText(error),
        theme: PopupAlertTheme.danger,
      });
    }
    this.props.setLoadingRequest(false);
  };

  render() {
    return (
      <form
        onSubmit={(e: React.FormEvent<HTMLFormElement>) => this.handleSubmit(e)}
        method="post"
      >
        <div className="pt-2 pb-4 w-full bg-gray-100 px-5 flex flex-col gap-2">
          <div className="flex flex-col">
            <label>Session</label>
            <input
              type="text"
              className={`text-left input-white w-full inline-block border-gray-500 border-2`}
              placeholder="Sub Category Title"
              value={capitalizeFirstLetter(
                this.props.sessions.session_plan_name
              )}
              disabled={true}
            />
          </div>
          <div className="flex flex-col">
            <label>Default pass marks</label>
            <select
              className="outline-none bg-white text-gray-700 text-base mr-1 py-2 px-2 border-2 border-gray-500 rounded"
              value={this.props.inputMarks}
              onChange={(e) => this.changeDefaultMarks(Number(e.target.value))}
            >
              {marksArray().map((item, index) => (
                <option
                  value={item ? item : "-1"}
                  key={index * (item ? item : -1)}
                >
                  {item}
                </option>
              ))}
            </select>
          </div>
          <div className="flex flex-col">
            <label>Customize pass marks by position</label>
            <div
              className={`text-left bg-white border-2 border-gray-500 rounded w-full ${
                this.state.choosePosition ? "h-auto" : "h-10"
              } ${
                this.state.formError &&
                this.state.formError.element === "position"
                  ? "border-red-500"
                  : ""
              } border-2 ${
                !this.props.loadingRequest
                  ? "cursor-pointer"
                  : "cursor-not-allowed"
              }`}
              title="Choose position"
              onClick={() => {
                !this.state.choosePosition && this.setChoosePosition(true);
                this.state.formError &&
                  this.state.formError.element === "position" &&
                  this.setFormError(null);
              }}
            >
              {/* DISPLAYING FILTERED SELECTING FILE ============================ */}
              {this.props.customMarks.length > 0 &&
              !this.state.choosePosition ? (
                <p className="my-1 mx-2 text-gray-700">
                  Some position has customized pass marks
                </p>
              ) : (
                !this.state.choosePosition && (
                  <p className="my-1 mx-2 text-gray-500">
                    Click to view position
                  </p>
                )
              )}
              {/* END DISPLAYING FILTERED SELECTING FILE ============================ */}

              {/* LISTING OF REJECTED DOCUMENTS ============================ */}
              {this.state.choosePosition && (
                <ChoosePosition
                  positionList={this.props.positionList}
                  setChoosePosition={this.setChoosePosition}
                  loadingRequest={this.props.loadingRequest}
                  defaultMarks={this.props.inputMarks}
                  customMarks={this.props.customMarks}
                  setCustomMarks={this.props.setCustomMarks}
                />
              )}
              {/* END LISTING OF REJECTED DOCUMENTS ============================ */}
            </div>
            {this.state.formError &&
              this.state.formError.element === "position" && (
                <span className="text-red-500">{this.state.formError.msg}</span>
              )}
          </div>
          <div className="flex flex-col">
            <label>Comment</label>
            <textarea
              placeholder={`Comment for pass mark`}
              cols={50}
              rows={3}
              className={`border-2 ${
                this.state.formError &&
                this.state.formError.element === "comment"
                  ? "border-red-400"
                  : "border-gray-500"
              } rounded px-3 py-1 w-full focus:outline-none`}
              style={{ resize: "none" }}
              value={this.props.inputComment}
              onChange={(e) => {
                this.props.setInputComment(e.target.value);
                this.setFormError(null);
              }}
              disabled={this.props.loadingRequest}
            ></textarea>
            {this.state.formError &&
              this.state.formError.element === "comment" && (
                <p className="text-red-800">{this.state.formError.msg}</p>
              )}
          </div>

          <div className="pt-4 flex justify-between">
            <button
              onClick={() => {
                this.cleanBeforeClose();
              }}
              className={`btn bg-gray-400 text-white hover:bg-gray-600 focus:outline-none ${
                this.props.loadingRequest && "cursor-not-allowed"
              }`}
              type="button"
              disabled={this.props.loadingRequest}
            >
              Exit
            </button>
            <div className="flex items-center gap-1">
              <button
                className={`btn bg-primary text-white hover:bg-blue-600 focus:outline-none ${
                  this.props.loadingRequest && "cursor-wait"
                }`}
                type="submit"
                disabled={this.props.loadingRequest}
              >
                {this.props.loadingRequest ? "Loading..." : "Save"}
              </button>
            </div>
          </div>
        </div>
      </form>
    );
  }
}

const mapStateToProps = ({
  auth,
}: StoreState): {
  auth: Auth;
} => {
  return {
    auth: auth,
  };
};

export default React.memo(
  connect(mapStateToProps, {
    createMarksPublication,
    PushPopupAlert,
  })(MarksPublicationForm)
);
